___CSS_LOADER_EXPORT___.push([module.id, `body{
  overflow-y: scroll;
}

main {
  
}

footer {
  position: fixed;
  display: flex;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--secondary-background-color);
  color: var(--secondary-text-color);
}

button {
  width: min-content;
  padding: 0.5rem 3rem;
  border-radius: 0.5rem;
  background-color: var(--primary-background-color);
  outline: 1px solid var(--primary-accent-color);
  outline-offset: -3px;
  font-weight: 900;
  color: white;
}

i {
  color: var(--secondary-text-color);
  font-style: normal;
}

a {
  color: white;
  transform-origin: left;
  position: relative;
  width: 100%;
  font-weight: 900;
  padding: 5px;
}
label{
  display: flex;
  gap: 0.5rem;
  color: var(--primary-text-color);
}

/* Width and Track */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background-color: var(--primary-background-color);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: var(--primary-accent-color);
  border-radius: 6px;
  border: 3px solid var(--primary-background-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: var(--primary-text-color);
}

/* Corner */
::-webkit-scrollbar-corner {
  background-color: var(--primary-background-color);
}

/* Basic styling for the tooltip */
[aria-label] {
  position: relative;
  cursor: pointer;
}

/* Styling for the tooltip content */
[aria-label]::before {
  content: attr(aria-label);
  /* Use the value of aria-label as the tooltip content */
  position: absolute;
  background-color: var(--primary-background-color);
  color: var(--primary-text-color);
  border-radius: 5px;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out 1s;
  font-size: small;
  font-weight: 600;
  padding: 0.25rem 0.75rem;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.8), 0px 5px 25px rgba(43, 43, 43, 0.5);
  z-index: 9999;
  pointer-events: none;
}

[label-position="bottom"]::before {
  top: 90%;
  right: 50%;
  transform: translateY(-100%) translateX(120%);
}

[label-position="left"]::before {
  top: 50%;
  right: 100%;
  transform: translateY(-50%) translateX(-15%);
}

[label-position="right"]::before {
  top: 0%;
  left: 100%;
  transform: translateY(10%) translateX(10%);
}

[label-position="top-left"]::before {
  top: 0%;
  right: 100%;
  transform: translateY(-50%) translateX(0%);
}

[label-position="bottom-right"]::before {
  top: 100%;
  left: 100%;
  transform: translateY(-50%) translateX(-50%);
}

/* Show the tooltip on hover */
[aria-label]:hover::before {
  visibility: visible;
  opacity: 1;
}

.indent {
  margin-left: 1.5rem;
}

h3 b {
  color: var(--secondary-accent-color) !important;
}

p b {
  color: purple;
}

p i {
  color: var(--primary-text-color);
}`, "",{"version":3,"sources":["webpack://./src/styles/global.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;AACpB;;AAEA;;AAEA;;AAEA;EACE,eAAe;EACf,aAAa;EACb,OAAO;EACP,QAAQ;EACR,SAAS;EACT,6CAA6C;EAC7C,kCAAkC;AACpC;;AAEA;EACE,kBAAkB;EAClB,oBAAoB;EACpB,qBAAqB;EACrB,iDAAiD;EACjD,8CAA8C;EAC9C,oBAAoB;EACpB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,kCAAkC;EAClC,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,kBAAkB;EAClB,WAAW;EACX,gBAAgB;EAChB,YAAY;AACd;AACA;EACE,aAAa;EACb,WAAW;EACX,gCAAgC;AAClC;;AAEA,oBAAoB;AACpB;EACE,WAAW;AACb;;AAEA;EACE,iDAAiD;AACnD;;AAEA,WAAW;AACX;EACE,6CAA6C;EAC7C,kBAAkB;EAClB,iDAAiD;AACnD;;AAEA,oBAAoB;AACpB;EACE,2CAA2C;AAC7C;;AAEA,WAAW;AACX;EACE,iDAAiD;AACnD;;AAEA,kCAAkC;AAClC;EACE,kBAAkB;EAClB,eAAe;AACjB;;AAEA,oCAAoC;AACpC;EACE,yBAAyB;EACzB,uDAAuD;EACvD,kBAAkB;EAClB,iDAAiD;EACjD,gCAAgC;EAChC,kBAAkB;EAClB,kBAAkB;EAClB,UAAU;EACV,uCAAuC;EACvC,gBAAgB;EAChB,gBAAgB;EAChB,wBAAwB;EACxB,8EAA8E;EAC9E,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,QAAQ;EACR,UAAU;EACV,6CAA6C;AAC/C;;AAEA;EACE,QAAQ;EACR,WAAW;EACX,4CAA4C;AAC9C;;AAEA;EACE,OAAO;EACP,UAAU;EACV,0CAA0C;AAC5C;;AAEA;EACE,OAAO;EACP,WAAW;EACX,0CAA0C;AAC5C;;AAEA;EACE,SAAS;EACT,UAAU;EACV,4CAA4C;AAC9C;;AAEA,8BAA8B;AAC9B;EACE,mBAAmB;EACnB,UAAU;AACZ;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,+CAA+C;AACjD;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gCAAgC;AAClC","sourcesContent":["body{\n  overflow-y: scroll;\n}\n\nmain {\n  \n}\n\nfooter {\n  position: fixed;\n  display: flex;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: var(--secondary-background-color);\n  color: var(--secondary-text-color);\n}\n\nbutton {\n  width: min-content;\n  padding: 0.5rem 3rem;\n  border-radius: 0.5rem;\n  background-color: var(--primary-background-color);\n  outline: 1px solid var(--primary-accent-color);\n  outline-offset: -3px;\n  font-weight: 900;\n  color: white;\n}\n\ni {\n  color: var(--secondary-text-color);\n  font-style: normal;\n}\n\na {\n  color: white;\n  transform-origin: left;\n  position: relative;\n  width: 100%;\n  font-weight: 900;\n  padding: 5px